<template>
  <div class="echart">
    <div class="left">
      <div class="tabAll">
        <div class="tabLeft">
          <div
            v-for="(item, index) in tab"
            :key="index"
            @click="changTab(item, index)"
            :class="{ isActive: index == tabIndex }"
          >
            {{ item }}
          </div>
        </div>
        <!-- <div class="tabRight"> -->
        <!-- <div v-for="(item, index) in eleTab" :key="index">{{ item }}</div> -->
        <!-- </div> -->
      </div>
    </div>
    <p style="font-weight: bold; font-size: 0.23rem; margin-top: 0.14rem">
      总量：{{ total }}{{ tabUnit[tabIndex] }}
    </p>
    <div class="right" style="flex-grow: 1" ref="echart"></div>
    <p style="color: #7aafff; font-size: 0.14rem">
      能耗折标系数：{{ tips }} 单位：吨标煤/吨
    </p>
  </div>
</template>
<script>
import { hourlyEnergyTrend } from "@/network/comprehensiveEnergyConsumption";
import { ehourlyEnergyConvert } from "@/network/environmenteProtecte";
export default {
  data() {
    return {
      tab: [
        "综合能耗",
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      tabIndex: 0,
      eleTab: ["绿电", "市电"],
      total: 0,
      tabUnit: ["吨标煤", "kWh", "吨", "吨", "吨", "m³", "m³", "m³", "m³"],
      tips: "",
    };
  },
  mounted() {
    this.ehourlyEnergyConvertFun();
  },
  methods: {
    /**
     * 获取标煤折算系数
     * **/
    ehourlyEnergyConvertFun() {
      ehourlyEnergyConvert(this.$getDate().year)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            let keys = Object.keys(content);
            this.tips = "";
            keys.forEach((item) => {
              this.tips += item + "（" + content[item] + "）";
            });
          }
        })
        .catch((err) => {});
    },
    changTab(item, index) {
      this.tabIndex = index;
      let msg = {
        tabName:this.tab[this.tabIndex],
        unit:this.tabUnit[this.tabIndex]
      }
      this.$bus.$emit("environmentRefsh", msg)
    },
    /**
     * 获取能耗用能数据
     * **/
    // hourlyEnergyTrendFun() {
    //   this.$parent.$parent.nhLoad = true;
    //   let fromDate = this.$getDate().year + "-01-01";
    //   let toDate = this.$getDate().year + "-12-31";
    //   hourlyEnergyTrend("月", this.tab[this.tabIndex], fromDate, toDate)
    //     .then((res) => {
    //       const { content, errcode, errmsg } = res;
    //       if (errcode == 0) {
    //         this.setEchart(content.list);
    //         /**传输能耗日历数据**/
    //         let msg = {
    //           name: this.tab[this.tabIndex],
    //           unit: this.tabUnit[this.tabIndex],
    //           value: content,
    //         };
    //         this.$bus.$emit("refshCalendar", msg);
    //       }
    //       this.$parent.$parent.nhLoad = false;
    //     })
    //     .catch((err) => {
    //       this.$parent.$parent.nhLoad = false;
    //     });
    // },
    setEchart(content) {
      let xAxisArry = [];
      let yAxisArry = [];
      this.total = 0;
      content.value.forEach((item,index) => {
        let month = (Number(index)+1) < 10 ? '0'+(Number(index)+1) : (Number(index)+1)
        xAxisArry.push(month);
        yAxisArry.push(item);
        this.total += Number(item);
      });
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          // textStyle: {
          //   color: "#fff",
          // },
          // bottom:'0%'
        },
        grid: {
          left: "3%",
          right: "3%",
          bottom: "12%",
          top: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            data: xAxisArry,
            // data:[0,0.5,0.3,0.1],
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            name: "单位："+content.unit,
            nameLocation: "end",
            nameTextStyle: {
              color: "#fff",
              padding: [0, 10, 0, 0],
            },
            nameGap: 15,
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: [
          //   {
          //   name: "当年",
          //   type: "bar",
          //   showBackground: true,
          //   z: 2,
          //   itemStyle: {
          //     normal: {
          //       color: this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
          //         { offset: 0, color: "rgba(255, 226, 0, 1)" }, //柱图渐变色
          //         { offset: 1, color: "rgba(255, 152, 0, 1)" }, //柱图渐变色
          //       ]),
          //     },
          //   },
          //   data: [220, 182, 191, 234, 290, 330, 310],
          // },
          {
            name: "",
            type: "line",
            z: 2,
            emphasis: {
              focus: "series",
            },
            lineStyle: {
              normal: {
                color: "#FFE200",
              },
            },
            itemStyle: {
              normal: {
                color: "#FFE200",
              },
            },
            data: yAxisArry,
            // data:[0.1,0.5,0.6,0.3,0,0.4]
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.tabAll {
  display: flex;
  justify-content: space-between;
  margin: 0.14rem 0.51rem 0 0.41rem;
}
.tabLeft,
.tabRight {
  display: flex;
  font-size: 0.16rem;
  > div {
    padding: 0.05rem 0.26rem;
    background: rgba(0, 54, 129, 1);
    margin: 0 0.01rem;
    cursor: pointer;
  }
  > div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  > div:last-child {
    border-radius: 0 0.04rem 0.04rem 0;
  }
}
.echart {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.isActive {
  background: #0655c6 !important;
}
</style>