<template>
  <div class="Calendar">
    <div class="cl_header">
      <p>{{ year }}</p>
      <span>单位：{{unit}}</span>
    </div>
    <div class="cl_month">
      <div v-for="(i, index) in valueArry" :key="index">
          <p>{{ index+1 }}月</p>
          <p>{{i}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      year: "",
      unit:'',
      valueArry:[]
    };
  },
  mounted() {
    this.year = new Date().getFullYear();
  },
  methods: {
    getData(msg){
      this.unit = msg.unit;
      this.valueArry = []
      msg.value.list.forEach(item=>{
        this.valueArry.push(this.toFixedTwo(item))
      })
      //小于等于12的补全0
      let length = this.valueArry.length
      for(let i = length;i<12;i++){
        this.valueArry.push(0)
      }
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = Number(val);
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>
<style scoped lang="less">
.Calendar {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.cl_header {
  display: flex;
  justify-content: center;
  position: relative;
    margin: 0.12rem 0 0.09rem;
  p {
    background: #0066ff;
    border-radius: 0.05rem;
    color: #fff;
    font-size: 0.2rem;
    padding: 0.05rem 0.23rem;
  }
  span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0.24rem;
    color: #5d89bf;
    font-size: 0.16rem;
  }
}
.cl_month {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
    margin-bottom: 0.09rem;
  > div {
   width: calc((100% - 0.3rem) / 3);
    background: linear-gradient(to bottom, rgba(3, 92, 229, 0.65), rgba(7, 49, 134, 0.31));
    border: 1px solid rgba(34, 175, 237, 0.5);
    border-radius: 0.05rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.035rem 0.05rem;
    box-sizing: border-box;
    flex-direction: column;
  }
  p:first-child{
      font-size: .2rem; 
      margin-bottom: .08rem; 
  }
  p:last-child{
      font-size: .18rem;  
      color: #00A6FF;
  }
}
</style>