import { request } from "./request";
import Vue from 'vue'

export function hourlyEnergyTrend(year) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyEnvironmental/hourlyEnergyTrend?id="+sessionStorage.getItem('companyId')+'&type='+sessionStorage.getItem('companyType')+'&year='+year,
        method: 'POST',
        headers: { showLoading: false },
    })
}
export function ehourlyEnergyConvert(year) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyEnvironmental/ehourlyEnergyConvert?id="+sessionStorage.getItem('companyId')+'&type='+sessionStorage.getItem('companyType')+'&year='+year,
        method: 'POST',
        headers: { showLoading: false },
    })
}
